.new-title {
  font-size: 8em;
  font-weight: 700;
  margin: 0;
  color: #FFFFFF;
  background: #fbf8ec;
  background: -webkit-linear-gradient(top, #FFFFFF 35%, #4e6773 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(35%, #FFFFFF), to(#4e6773));
  background: -o-linear-gradient(top, #FFFFFF 35%, #4e6773 100%);
  background: linear-gradient(to bottom, #FFFFFF 35%, #4e6773 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: .5;
  z-index: 10;
  position: relative;
  top: 135px;
  /*font-size:60%;*/
}

.under-title {
  font-size: 8em;
  font-weight: 700;
  margin: 0;
  opacity: .9;
  
  /*font-size: 60%;*/
  /*top:-100px;*/
 
}

title-brand{
  position: absolute;
  height: 10px;
}

button{
  font-size: 2em;
  font-weight: 700;
  color: grey;
  border-radius: 7px;
  -webkit-box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .2);
          box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .2);
  margin: 10px;
  opacity: .8
}

#submitField{
  height: 40px;
  opacity: .8;

}

.mode-buttons {
  margin-bottom: 50px
}


